@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.App{
  width: 100vw;
  font-family: 'Roboto', sans-serif;
}

.title{
  text-align: center;
  margin: auto;
  font-size: 32pt;
  font-weight: bold;
  width: 100%;
}

.sub-heading{
  text-align: center;
  font-size: 24pt;
  font-weight: bold;
}

.txt{
  margin: auto;
  text-align: center;
  font-size: 16pt;
  width: 75%;
}

.fig-description{
  margin: auto;
  text-align: center;
  font-size: 14pt;
  width: 50%;
  font-style: italic;
}

.img{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 300px;
}
